<template>

  <div style="width: 1200px;margin: 5px auto">
    <div style="margin: 20px 0 0 0">
      <div style="text-align: center">
        <el-input size="small" style="width: 700px" placeholder="请输入名称" v-model="name" suffix-icon="el-icon-search"></el-input>&nbsp;
        <!--        <el-input size="medium" style="width: 300px" placeholder="请输入评分" v-model="mark" suffix-icon="el-icon-search"></el-input>&nbsp;-->
        <!--        <el-input size="medium" style="width: 300px" placeholder="请输入年份" v-model="age" suffix-icon="el-icon-search"></el-input>-->
        <el-button size="small" class="ml-5" type="info" @click="load">搜索</el-button>
      </div>
    </div>

    <div style="margin-top: 20px;">
      <el-tabs v-model="type" @tab-click="handleClickType">
        <el-tab-pane disabled>
          <span slot="label" style="font-size: 15px;color: #8c939d">类型</span>
        </el-tab-pane>
        <el-tab-pane v-for="item in types" :label="item.name"></el-tab-pane>
      </el-tabs>
    </div>
    <div>
      <el-row :gutter="10" style="margin: 10px 0">
        <el-col :span="6" v-for="item in tableData" :key="item.id" style="margin-bottom: 10px">
          <div style="border: 1px solid #ccc; padding-bottom: 10px; border-radius: 10px; overflow: hidden">
            <img :src="item.img" alt="" style="width: 100%;height: 170px; cursor: pointer">
            <div style="padding: 5px; font-size: 18px; cursor: pointer;text-align: center;color: #494949"
                 @click="$router.push('/front/musicDetail?id=' + item.id)">
              <b>{{ item.name }}</b>
            </div>
          </div>
        </el-col>
      </el-row>

      <div style="padding: 10px 0">
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-sizes="[2, 5, 10, 20]"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="total">
        </el-pagination>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  name: "Film",
  data() {
    return {
      tableData: [],
      total: 0,
      pageNum: 1,
      pageSize: 12,
      name: "",
      mark: 99999,
      age: 9999,
      form: {},
      user: localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {},
      type: 0,
      types: [],
      timesData: []
    }
  },
  created() {
    this.type = 0;

    this.load()


  },
  methods: {
    handleClickType(tab, event) {
      this.type = this.types[tab.index - 1].id
      this.load();    },
    load() {
      console.log(this.type)
      this.request.get("/music/page/type/", {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          name: this.name,
          typeId: this.type
        }
      }).then(res => {
        this.tableData = res.data.records
        this.total = res.data.total
      })

      this.request.get("/type").then(res => {
        this.types = res.data
      })

    },
    handleSizeChange(pageSize) {
      console.log(pageSize)
      this.pageSize = pageSize
      this.load()
    },
    handleCurrentChange(pageNum) {
      console.log(pageNum)
      this.pageNum = pageNum
      this.load()
    },
  }
}
</script>

<style>
/deep/ .el-tabs__item {
  /* 修改为您想要的文字大小 */
  font-size: 30px !important;
}


</style>
